  color: #fff;
  transition: 0.3s;
  letter-spacing: normal;
}

.contact-item__link:hover {
  color: #fe4a49;
}

.navigation {
  background: #fff;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 150;
}

.navigation.is-fixed {
  position: fixed;
}

.navigation__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 75px;
  border: 1px solid rgba(99, 114, 130, 0.2);
}

.nav-logo {
  flex: 1 1 20%;
