}

.toggle-menu {
  display: none;
  width: 35px;
  height: 27px;
  margin-right: 40px;
  cursor: pointer;
  background: none;
}

.toggle {
  display: block;
  content: '';
  width: 100%;
  height: 5px;
  background: #0a131c;
  border-radius: 4px;
  margin-bottom: 4px;
}

.toggle:last-child {
  margin-bottom: 0;
}

.nav-menu {
  display: flex;
