//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"),
	url("../fonts/" + $file + ".woff") format("woff") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START Arboria-Black
@font-face {
	@include fontdefault(normal, 800, 'Arboria');
	@include fontpath('Arboria-Black', 'Arboria-Black', 'Arboria-Black'); }

@font-face {
	@include fontdefault(italic, 800, 'Arboria');
	@include fontpath('Arboria-BlackItalic', 'Arboria-BlackItalic', 'Arboria-BlackItalic'); }

